@use '../../styles/index.scss' as *;

.footer {
  background-color: $color-black;
  display: flex;
  flex-direction: column;
  align-items: center;
  padding-bottom: 20px;

  &__img {
    width: 95px;
    height: 80px;
  }

  &__text {
    margin-top: 5px;
    font-size: 18px;
    font-weight: 800;
    line-height: 1;
    color: $color-yellow;
  }
}
